import { Fragment, VFC } from 'react';
import IconName from './iconName';

type Color =
  | 'primary'
  | 'secondary'
  | 'success'
  | 'info'
  | 'danger'
  | 'warning'
  | 'light'
  | 'dark'
  | 'white';

type Size =
  | 'sm'
  | 'md'
  | 'lg'

interface ButtonProps {
  color?: Color;
  isGradient?: boolean;
  isOutline?: boolean;
  size?: Size;
  label: string;
  icon?: IconName;
}

const getBackgroundColor = (props: ButtonProps): string => {
  const { color, isGradient, isOutline } = props;
  const buttonColor = color || 'primary';

  if (isOutline) {
    return `btn-outline-${buttonColor} `;
  }

  return `btn${isGradient ? '-gradient' : ''}-${buttonColor} `;
};

const getSize = (props: ButtonProps): string => {
  const { size } = props;
  const buttonSize = size || 'md';
  switch (buttonSize) {
    case 'sm': return 'btn-sm';
    case 'lg': return 'btn-lg';
    default: return '';
  }
}

const Button: VFC<ButtonProps> = (props) => {
  const { label, icon } = props;
  const backgroundColor = getBackgroundColor(props);
  const size = getSize(props);
  const iconElement = icon ? 'btn-icon' : '';
  const className = `btn ${backgroundColor}${size} ${iconElement}`;

  return (
    <button
      type="button"
      className={`${className} w-auto me-1 mb-0`}
    >
      {icon ?
        <div className="d-flex align-items-center">
          <i className={`ni ni-${icon} me-2`} aria-hidden="true"></i>
          {label}
        </div>
        :
        <Fragment>
          {label}
        </Fragment>
      }
    </button>
  );
};

export default Button;
